export default {
	data() {
		return {
			tabList:[{
				id:1,
				name:'我的卡券'
			},{
				id:2,
				name:'历史卡券'
			}],
			currentTab:0,
			showH5: true,
			cardList:[],//可用卡券
			wasteCardList: [], //历史卡券
			memberId: '',
		};
	},
	onShow(){
		if(navigator){
			this.showH5 = true
		}else{
			this.showH5 = false
		}
		this.memberId = JSON.parse(uni.getStorageSync('userInfo')).id
		this.getCardList()
	},
	methods:{
		// 获取当前页面的信息
		getCardList(){
			this.$Request.get(`/appCardController.do?getCardList&memberId=${this.memberId}`)
			.then(res => {
				if(res.code == 0){
					this.cardList =res.data.cardList.map(item => {
						return {
							...item,
							expiresDate: item.expiresDate && item.expiresDate.substring(0,10)
						}
					})
					this.wasteCardList = res.data.wasteCardList.map(item => {
						return {
							...item,
							useDate: item.useDate && item.useDate.substring(0,10)
						}
					})
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		clickTab(index){
			this.currentTab = index
		},
		// 立即领取
		chooseCard(item){
			let vm = this
			uni.showModal({
			    title: '立即兑换',
			    content: `立即获得${item.cardValue}点能量`,
				cancelText: '暂不兑换',
				confirmText: '立即兑换',
			    success: function (res) {
			        if (res.confirm) {
						vm.$Request.get(`/appCardController.do?useEnergyCard&memberId=${vm.memberId}&cardId=${item.id}`)
						.then(res => {
							if(res.code == 0){
								vm.getCardList()
							}else{
								uni.showToast({
									title: res.info,
									icon: 'none'
								})
							}
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		// 立即使用
		chooseMoney(item){
			uni.navigateTo({
				url:`/pages/mine/superMember?price=${item.cardValue}`
			})
		}
	}
}
